import test from 'ava';
import add from './add';

test('Empty string', t => {
  t.is(add(''), 0);
});

test('One number', t => {
  t.is(add('5'), 5);
});

test('Two numbers', t => {
  t.is(add('5,3'), 8);
});

test('Unknown amount of numbers', t => {
  t.is(add('2,4,7,10'), 23);
});

test('Unknown amount of numbers width delimeter \\n', t => {
  t.is(add('2,4\n7,10'), 23);
});

test('Unknown amount of numbers width delimeter \\n\\r', t => {
  t.is(add('2,4\n\r7,10'), 23);
});

test('Unknown amount of numbers width delimeter \\r', t => {
  t.is(add('2,4\r7,10'), 23);
});

test('Unknown amount of numbers width delimeter <br>', t => {
  t.is(add('2,4<br>7,10'), 23);
});
